'use client';

import { useState, useEffect } from 'react';
import {
  Home as HomeIcon,
  Link as LinkIcon,
  Power as PowerIcon,
  Sliders as SlidersIcon,
  Check as CheckIcon,
  Zap as ZapIcon,
  Reload as ReloadIcon,
  Bug as BugIcon,
  Lightbulb as LightbulbIcon,
  Code as CodeIcon,
  Plus as PlusIcon,
  ChevronLeft as ChevronLeftIcon
} from 'pixelarticons/fonts/react';

type User = {
  login: string;
  avatar_url: string;
  name: string;
};

type Repo = {
  id: number;
  name: string;
  full_name: string;
  owner: { login: string };
  open_issues_count: number;
};

type Issue = {
  id: number;
  number: number;
  title: string;
  labels: { name: string }[];
};

export default function Home() {
  const [user, setUser] = useState<User | null>(null);
  const [repos, setRepos] = useState<Repo[]>([]);
  const [selectedRepo, setSelectedRepo] = useState<Repo | null>(null);
  const [issues, setIssues] = useState<Issue[]>([]);
  const [selectedIssue, setSelectedIssue] = useState<Issue | null>(null);
  const [isProvisioning, setIsProvisioning] = useState(false);
  const [loading, setLoading] = useState(true);
  const [hiddenRepos, setHiddenRepos] = useState<Set<number>>(new Set());
  const [manageMode, setManageMode] = useState(false);

  useEffect(() => {
    // Check if we have a GitHub token
    const token = localStorage.getItem('github_token');
    if (token) {
      fetchUser(token);
    } else {
      setLoading(false);
    }

    // Load hidden repos from localStorage
    const hidden = localStorage.getItem('hidden_repos');
    if (hidden) {
      setHiddenRepos(new Set(JSON.parse(hidden)));
    }
  }, []);

  const fetchUser = async (token: string) => {
    try {
      const res = await fetch('https://api.github.com/user', {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (res.ok) {
        const userData = await res.json();
        setUser(userData);
        fetchRepos(token);
      } else {
        localStorage.removeItem('github_token');
        setLoading(false);
      }
    } catch (e) {
      console.error('Failed to fetch user:', e);
      setLoading(false);
    }
  };

  const fetchRepos = async (token: string) => {
    try {
      // Fetch all repos with pagination
      let allRepos: Repo[] = [];
      let page = 1;
      let hasMore = true;

      while (hasMore && page <= 10) { // Max 10 pages = 1000 repos
        const res = await fetch(`https://api.github.com/user/repos?sort=pushed&per_page=100&page=${page}`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (res.ok) {
          const repoData = await res.json();

          if (repoData.length === 0) {
            hasMore = false;
          } else {
            allRepos = [...allRepos, ...repoData];
            page++;
          }
        } else {
          hasMore = false;
        }
      }

      setRepos(allRepos);
    } catch (e) {
      console.error('Failed to fetch repos:', e);
    } finally {
      setLoading(false);
    }
  };

  const fetchIssues = async (repo: Repo) => {
    const token = localStorage.getItem('github_token');
    if (!token) return;

    try {
      const res = await fetch(`https://api.github.com/repos/${repo.full_name}/issues?state=open`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (res.ok) {
        const issueData = await res.json();
        setIssues(issueData);
        setSelectedRepo(repo);
      }
    } catch (e) {
      console.error('Failed to fetch issues:', e);
    }
  };

  const connectGitHub = () => {
    // For now, let's use GitHub's device flow or redirect to OAuth
    // We'll need a backend endpoint for this, but for POC let's use a token input
    const token = prompt('Enter your GitHub Personal Access Token (will be stored locally):');
    if (token) {
      localStorage.setItem('github_token', token);
      fetchUser(token);
    }
  };

  const disconnect = () => {
    localStorage.removeItem('github_token');
    setUser(null);
    setRepos([]);
    setSelectedRepo(null);
    setIssues([]);
  };

  const toggleHideRepo = (repoId: number) => {
    const newHidden = new Set(hiddenRepos);
    if (newHidden.has(repoId)) {
      newHidden.delete(repoId);
    } else {
      newHidden.add(repoId);
    }
    setHiddenRepos(newHidden);
    localStorage.setItem('hidden_repos', JSON.stringify(Array.from(newHidden)));
  };

  const visibleRepos = repos.filter(r => !hiddenRepos.has(r.id));
  const displayRepos = manageMode ? repos : visibleRepos;

  // Loading state
  if (loading) {
    return (
      <div className="fixed inset-0 w-screen h-screen bg-[rgb(var(--color-orange))] flex items-center justify-center">
        <div className="text-9xl animate-spin-slow">âš¡</div>
      </div>
    );
  }

  // Not connected to GitHub
  if (!user) {
    return (
      <div className="fixed inset-0 w-screen h-screen bg-[rgb(var(--color-orange))] overflow-hidden">
        <div className="h-full flex flex-col items-center justify-center p-6 safe-area-inset">
          {/* Logo/Header */}
          <div className="card-yellow shadow-retro-xl p-12 mb-12 text-center">
            <div className="mb-8 flex justify-center">
              <HomeIcon className="w-32 h-32" />
            </div>
            <h1 className="text-8xl text-[rgb(var(--color-black))] mb-6">HOMESTEAD</h1>
            <p className="text-4xl text-[rgb(var(--color-black))] font-black">BUILD ANYWHERE</p>
          </div>

          {/* Connect Button */}
          <button
            onClick={connectGitHub}
            className="card-white shadow-retro-xl p-12 active:translate-x-4 active:translate-y-4 active:shadow-none transition-all"
          >
            <div className="flex flex-col items-center gap-6">
              <LinkIcon className="w-32 h-32" />
              <span className="text-5xl text-[rgb(var(--color-black))]">
                CONNECT GITHUB
              </span>
            </div>
          </button>

          <p className="text-2xl text-[rgb(var(--color-black))] font-black mt-12 text-center">
            Tend your digital homestead
          </p>
        </div>
      </div>
    );
  }

  // Issue detail view
  if (selectedIssue) {
    return (
      <div className="fixed inset-0 w-screen h-screen bg-[rgb(var(--color-orange))] overflow-hidden">
        <div className="h-full flex flex-col p-6 safe-area-inset">
          {/* Back Button */}
          <button
            onClick={() => setSelectedIssue(null)}
            className="card-yellow shadow-retro-lg p-4 mb-8 active:translate-x-2 active:translate-y-2 active:shadow-none transition-all"
          >
            <ChevronLeftIcon className="w-12 h-12" />
          </button>

          {/* Issue Header */}
          <div className="card-white shadow-retro-xl p-8 mb-8">
            <h1 className="text-7xl text-[rgb(var(--color-black))] mb-4 leading-none">
              #{selectedIssue.number}
            </h1>
            <p className="text-3xl text-[rgb(var(--color-black))] font-black leading-tight">
              {selectedIssue.title}
            </p>
          </div>

          {/* Action Area */}
          <div className="flex-1 flex flex-col justify-center">
            {!isProvisioning ? (
              <button
                onClick={() => setIsProvisioning(true)}
                className="card-yellow shadow-retro-xl p-12 active:translate-x-4 active:translate-y-4 active:shadow-none transition-all"
              >
                <div className="flex flex-col items-center gap-6">
                  <ZapIcon className="w-32 h-32" />
                  <span className="text-6xl text-[rgb(var(--color-black))] leading-none">
                    START
                  </span>
                </div>
              </button>
            ) : (
              <div className="card-white shadow-retro-xl p-8">
                <div className="text-center mb-10">
                  <div className="mb-6 flex justify-center">
                    <ReloadIcon className="w-32 h-32 animate-spin-slow" />
                  </div>
                  <h2 className="text-5xl text-[rgb(var(--color-black))] mb-3">
                    LOADING...
                  </h2>
                  <p className="text-2xl text-[rgb(var(--color-black))]/60 font-bold">
                    ~2 MIN
                  </p>
                </div>

                {/* Progress Steps */}
                <div className="space-y-6">
                  {[
                    { label: 'CREATE', done: true },
                    { label: 'CLONE', done: true },
                    { label: 'INSTALL', done: false },
                    { label: 'START', done: false },
                  ].map((step, i) => (
                    <div key={i} className="flex items-center gap-6">
                      <div className={`w-16 h-16 border-6 border-[rgb(var(--color-black))] flex items-center justify-center transition-all ${
                        step.done
                          ? 'bg-[rgb(var(--color-green))] scale-100'
                          : 'bg-[rgb(var(--color-white))] scale-90'
                      }`}>
                        {step.done ? <CheckIcon className="w-10 h-10" /> : <ReloadIcon className="w-8 h-8 animate-spin-slow" />}
                      </div>
                      <span className="text-3xl font-black text-[rgb(var(--color-black))]">
                        {step.label}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Issues list view
  if (selectedRepo) {
    return (
      <div className="fixed inset-0 w-screen h-screen bg-[rgb(var(--color-orange))] overflow-y-auto">
        <div className="min-h-full flex flex-col p-6 safe-area-inset">
          {/* Back Button */}
          <button
            onClick={() => {
              setSelectedRepo(null);
              setIssues([]);
            }}
            className="card-yellow shadow-retro-lg p-4 mb-8 active:translate-x-2 active:translate-y-2 active:shadow-none transition-all"
          >
            <ChevronLeftIcon className="w-12 h-12" />
          </button>

          {/* Repo Header */}
          <div className="card-white shadow-retro-xl p-8 mb-8">
            <h1 className="text-6xl text-[rgb(var(--color-black))] leading-none mb-4">
              {selectedRepo.name}
            </h1>
            <p className="text-2xl text-[rgb(var(--color-black))]/60 font-black">
              {selectedRepo.owner.login}
            </p>
          </div>

          {/* Create Issue Button */}
          <button
            onClick={() => {
              const title = prompt('Issue title:');
              if (title) {
                alert('Create issue feature coming soon!\n\nFor now, create issues on GitHub:\n' + `https://github.com/${selectedRepo.full_name}/issues/new`);
              }
            }}
            className="w-full card-yellow shadow-retro-xl p-8 mb-6 active:translate-x-4 active:translate-y-4 active:shadow-none transition-all"
          >
            <div className="flex items-center justify-center gap-4">
              <PlusIcon className="w-16 h-16" />
              <span className="text-4xl text-[rgb(var(--color-black))]">CREATE ISSUE</span>
            </div>
          </button>

          {/* Issues */}
          <div className="space-y-6 pb-6">
            {issues.length === 0 ? (
              <div className="card-white shadow-retro-xl p-12 text-center">
                <p className="text-4xl text-[rgb(var(--color-black))] font-black">
                  NO ISSUES YET
                </p>
                <p className="text-2xl text-[rgb(var(--color-black))]/60 font-bold mt-4">
                  Create one above!
                </p>
              </div>
            ) : (
              issues.map((issue) => (
                <button
                  key={issue.id}
                  onClick={() => setSelectedIssue(issue)}
                  className="w-full card-white shadow-retro-lg p-8 active:translate-x-2 active:translate-y-2 active:shadow-none transition-all text-left"
                >
                  <div className="flex items-start gap-6">
                    {issue.labels.some(l => l.name.includes('bug')) ? (
                      <BugIcon className="w-16 h-16 flex-shrink-0" />
                    ) : issue.labels.some(l => l.name.includes('feature')) ? (
                      <LightbulbIcon className="w-16 h-16 flex-shrink-0" />
                    ) : (
                      <CodeIcon className="w-16 h-16 flex-shrink-0" />
                    )}
                    <div className="flex-1">
                      <div className="text-4xl font-black text-[rgb(var(--color-black))] mb-2">
                        #{issue.number}
                      </div>
                      <div className="text-2xl text-[rgb(var(--color-black))] font-bold leading-tight">
                        {issue.title}
                      </div>
                    </div>
                  </div>
                </button>
              ))
            )}
          </div>
        </div>
      </div>
    );
  }

  // Repos list view
  return (
    <div className="fixed inset-0 w-screen h-screen bg-[rgb(var(--color-orange))] overflow-y-auto">
      <div className="min-h-full flex flex-col p-6 safe-area-inset">
        {/* Header with user */}
        <div className="card-yellow shadow-retro-xl p-8 mb-6">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-4">
              <HomeIcon className="w-20 h-20" />
              <h1 className="text-7xl text-[rgb(var(--color-black))]">HOMESTEAD</h1>
            </div>
            <button
              onClick={disconnect}
              title="Disconnect"
            >
              <PowerIcon className="w-12 h-12" />
            </button>
          </div>
          <p className="text-3xl text-[rgb(var(--color-black))] font-black">
            {user.name || user.login}
          </p>
        </div>

        {/* Manage Repos Button */}
        <button
          onClick={() => setManageMode(!manageMode)}
          className="card-white shadow-retro-xl p-6 mb-6 active:translate-x-4 active:translate-y-4 active:shadow-none transition-all"
        >
          <div className="flex items-center justify-center gap-4">
            {manageMode ? <CheckIcon className="w-14 h-14" /> : <SlidersIcon className="w-14 h-14" />}
            <span className="text-3xl text-[rgb(var(--color-black))] font-black">
              {manageMode ? 'DONE' : 'MANAGE REPOS'}
            </span>
          </div>
        </button>

        {/* Repos */}
        <div className="space-y-6 pb-8">
          {displayRepos.length === 0 ? (
            <div className="card-white shadow-retro-xl p-12 text-center">
              <p className="text-4xl text-[rgb(var(--color-black))] font-black">
                {repos.length === 0 ? 'NO REPOS FOUND' : 'ALL REPOS HIDDEN'}
              </p>
              {repos.length > 0 && (
                <p className="text-2xl text-[rgb(var(--color-black))]/60 font-bold mt-4">
                  Click MANAGE REPOS to show
                </p>
              )}
            </div>
          ) : (
            displayRepos.map((repo) => {
              const isHidden = hiddenRepos.has(repo.id);

              return (
                <div key={repo.id} className="relative">
                  <button
                    onClick={() => {
                      if (manageMode) {
                        toggleHideRepo(repo.id);
                      } else {
                        fetchIssues(repo);
                      }
                    }}
                    className={`w-full card-white shadow-retro-xl p-8 active:translate-x-4 active:translate-y-4 active:shadow-none transition-all text-left ${
                      manageMode && isHidden ? 'opacity-40' : ''
                    }`}
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-6">
                        {manageMode && (
                          <div className={`w-16 h-16 border-6 border-[rgb(var(--color-black))] flex items-center justify-center ${
                            isHidden ? 'bg-white' : 'bg-[rgb(var(--color-green))]'
                          }`}>
                            {isHidden ? <div className="w-8 h-8 border-4 border-[rgb(var(--color-black))] rounded-sm" /> : <CheckIcon className="w-10 h-10" />}
                          </div>
                        )}
                        <div>
                          <div className="text-5xl font-black text-[rgb(var(--color-black))] mb-2">
                            {repo.name}
                          </div>
                          <div className="text-xl text-[rgb(var(--color-black))]/60 font-black">
                            {repo.owner.login}
                          </div>
                        </div>
                      </div>
                      {!manageMode && (
                        <div className="card-yellow shadow-retro p-4">
                          <span className="text-4xl font-black text-[rgb(var(--color-black))]">
                            {repo.open_issues_count}
                          </span>
                        </div>
                      )}
                    </div>
                  </button>
                </div>
              );
            })
          )}
        </div>
      </div>
    </div>
  );
}
